.Action {
    display: flex;
    width: 25px;
    height: 25px;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    touch-action: none;
    cursor: var(--cursor, pointer);
    border-radius: 5px;
    appearance: none;
    background-color: white;
    -webkit-tap-highlight-color: transparent;
}