@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    font-size: 16px;
}

body {
    position: relative;
    background-color: #f0f3f5;
}

#mainMenu {
    height: calc(100vh - 24px);
}

#backdrop {
    visibility: hidden;
    background-color: #141f33;
    opacity: 0;
}

#mainMenu:hover+#backdrop {
    visibility: visible;
    opacity: .7;
}


.scroll::-webkit-scrollbar {
    width: 6px;
    /* ширина скрола */
    height: 6px;
}

/* Кнопки стрілок */
.scroll::-webkit-scrollbar-button {
    background: #ffffff;
    width: 0;
    height: 0;
}

/* Шпильки треку */
.scroll::-webkit-scrollbar-thumb {
    background: #1b77d29e;
    border-radius: 3px;
}

/* Фон треку */
.scroll::-webkit-scrollbar-track {
    background: #ffffff;
}